<template>
	<div id="news-detail">
		<public-header title="公告详情"></public-header>
		<div class="content">
			<div class="detail-info" v-html="content">
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				id: 0,
				content: '',
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					data: {
						id: that.id
					},
					url: 'manage/news/notice/detail',
					success: (res) => {
						that.content = res.notice.content;
					}
				});
			},
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#news-detail .content{
		background: #fff;
	}
	#news-detail .content .detail-info{
		padding: 10px;
	}
</style>


